.Header_header__2b05g {
    width: 100%;
    height: 5rem;
    background-color: #044599;
    padding: 0 10%;
  }
  
  .Header_header__2b05g nav {
    height: 100%;
  }
  
  .Header_header__2b05g ul {
    height: 100%;
    list-style: none;
    display: flex;
    padding: 0;
    margin: 0;
    align-items: center;
    justify-content: center;
  }
  
  .Header_header__2b05g li {
    margin: 0 1rem;
    width: 5rem;
  }
  
  .Header_header__2b05g a {
    color: white;
    text-decoration: none;
  }
  
  .Header_header__2b05g a:hover,
  .Header_header__2b05g a:active,
  .Header_header__2b05g a.Header_active__1M76Y {
    color: #95bcf0;
    padding-bottom: 0.25rem;
    border-bottom: 4px solid #95bcf0;
  }
  .form-inline {
    display: flex;
    width: 85%;
    margin-left: 100px;
    margin-top: 1%;
    border: 1px solid black;
    padding: 1%;
    /* background: radial-gradient(circle at -8.9% 51.2%, rgb(255, 124, 0) 0%, rgb(255, 124, 0) 15.9%, rgb(255, 163, 77) 15.9%, rgb(255, 163, 77) 24.4%, rgb(19, 30, 37) 24.5%, rgb(19, 30, 37) 66%); */
    background: linear-gradient(109.6deg, rgb(0, 0, 0) 11.2%, rgb(11, 132, 145) 91.1%);
    border-radius: 15px
  }
  .form-inline input {
    width: 100%;
  }
  
  .form-inline button{
   margin-right: 10%;
   margin-left: 10px;
